import React, { useState, useEffect, useRef } from "react";
import {
  ProductDetailsWrapper,
  TitleWrapper,
  VueLogoImg,
  FullContainer,
  AffinityContainer,
  UserAffinityTitle,
  AffinityCloseButtonContainer,
} from "./styled";
import {
  RenderRecommendationContainer,
  RenderTopcontainer,
  RenderSpiderContainer,
  RenderStyleItContainer,
  RenderSparkLine,
  RenderWordCloud,
  RenderAffinityContainer,
} from "./renderProps";
import {
  RecommendedPdtsContainer,
  StyleItPdtsContainer,
  ConfigConsumer,
} from "../../container";
import VueCommerceLogo from "../../static/svg/vue-commerce.svg";
import VueStyleLogo from "../../static/svg/vue-style.svg";
import { Button } from "semantic-ui-react";
import { getCookie } from "../../common";
import "semantic-ui-css/semantic.min.css";

const ProductDetails = (props) => {
  const render = useRef(false);
  const [widgetList, updateWidgetList] = useState([]);
  const [currWidget, updateWidget] = useState({});
  const [ontology, updateOntology] = useState("");
  const [copyWidgetArr, updateCopyWidgetArr] = useState([]);

  const [displayOccasion, updateDisplayOccasion] = useState("");
  const [OccasionStyleData, updateOccasionStyleData] = useState([]);
  const [AllOccasionDetails, updateAllOccasionDetails] = useState([]);
  const [hasOnlyOccasionName, updateHasOnlyOccasionName] = useState([]);
  const [isModalOpen, updateModal] = useState(false);
  const [sparkLineArr, updateSparkLineArr] = useState([]);
  const [is_affinity_checked, update_affinity_checkbox] = useState(false);
  const [isChecked, updateCheckbox] = useState(false);
  const [filterValue, updateFilterValue] = useState("");

  const [imageSearchData, updateimageSearchData] = useState({
    imgSrc: "",
    tagsResp: {},
    file: null,
  });
  const productId = useRef("");

  let debug = false;
  let qp =
    window.location && window.location.search
      ? window.location.search.replace("?", "")
      : "";
  qp = qp.split("&");
  qp.map((param) => {
    if (param) {
      let paramArr = param.split("=");
      if (paramArr[0] && paramArr[1]) {
        if (
          paramArr[0] === "id" &&
          paramArr[1] &&
          paramArr[1] !== productId.current
        ) {
          productId.current = paramArr[1];
          render.current = false;
        } else if (paramArr[0] === "force_debug") {
          debug = paramArr[1] === "true";
        }
      }
    }
    return param;
  });

  useEffect(() => {
    if (widgetList && widgetList.length) {
      let _widget = JSON.parse(JSON.stringify(widgetList[0]));
      if (
        filterValue &&
        filterValue != "" &&
        (_widget["widgetList"].includes("0_enableBundit") ||
          _widget["widgetList"].includes("0_disableBundit"))
      ) {
        _widget.postBody = _widget.postBody + "&" + filterValue;
      }
      updateWidget(_widget);
      let new_widget_list = widgetList.slice(1);
      updateWidgetList(new_widget_list);
    }
  }, [widgetList]);

  return (
    <>
      {/* <TitleWrapper>
            <VueLogoImg src={VueCommerceLogo} alt='Vue Commerce Logo' title='Vue Commerce' />
            <VueLogoImg src={VueStyleLogo} alt='Vue Style Logo' title='Vue Style' />
        </TitleWrapper> */}
      <ConfigConsumer>
        {(context) => {
          const { productDetails } = context;
          let API_KEY = context.API_KEY;
          if (productDetails.API_KEY) {
            API_KEY = productDetails.API_KEY;
          }
          const {
            endPoint,
            productRecommendation,
            similarRecommendation,
            personalizedRecommendation,
            hideTagResults,
          } = productDetails;
          let baseEndPoint = context.url;
          if (endPoint) {
            baseEndPoint = endPoint;
          }

          const mad_UUID = getCookie("mad_UUID");
          let user_id = localStorage.getItem("userId");
          if (!user_id) {
            user_id = context.user_id;
          }
          if (
            !render.current &&
            user_id &&
            API_KEY &&
            mad_UUID &&
            baseEndPoint &&
            productRecommendation &&
            similarRecommendation &&
            personalizedRecommendation
          ) {
            if (hasOnlyOccasionName && hasOnlyOccasionName.length) {
              updateDisplayOccasion("");
              updateOccasionStyleData([]);
              updateAllOccasionDetails([]);
              updateHasOnlyOccasionName([]);
              updateModal(false);
              updateSparkLineArr([]);
              updateCheckbox(false);
            }
            window.scrollTo(0, 0);
            render.current = true;
            let widget_arr = [];

            // if (context.client_name == '14_namshi') {
            //     widget_arr.push({
            //         'widgetList': ['4'],
            //         'postBody': `api_key=${API_KEY}&cp_ensemble=true&details=true&duplicates=true&fields=["id","product_id","category","ontology","link","image_link","internal_image_url","small_image_link","msd_ontology"]&mad_uuid=${mad_UUID}&user_id=${user_id}&num_results=[15]&require_source=true&widget_list=[4]&product_id=${productId.current}&brand_filter=false&cp_random=true&user_boost_params=&is_from_demo_site=true&force_debug=true`,
            //         'recommendationUrl': baseEndPoint + productRecommendation
            //     })
            // }
            if (
              context.vue_user &&
              context.vue_user.userAffinityPage &&
              context.vue_user.userAffinityUrl.length > 0
            ) {
              widget_arr.push({
                widgetList: ["user_affinity"],
                postBody: `api_key=${API_KEY}&mad_uuid=${mad_UUID}&user_id=${user_id}`,
                recommendationUrl: context.vue_user.userAffinityUrl,
              });
            }

            if (context.client_name == "2253_yoox-poc") {
              widget_arr.push({
                widgetList: ["9"],
                postBody: `api_key=${API_KEY}&cp_ensemble="false"&details=true&fields=["id","product_id","category","ontology","link","image_link","internal_image_url","small_image_link","msd_ontology"]&mad_uuid=${mad_UUID}&user_id=${user_id}&num_results=4&products_per_outfit=3&group_by_occasion=true&occasions=["collection_1106","collection_1107","collection_1110"]&widget_list=[9]&product_id=${productId.current}&manually_curated="true"&out_of_stock_fallback="fill"&machine_generated_fallback="true"`,
                recommendationUrl: baseEndPoint + "/vuestyle/widgets",
              });
              widget_arr.push({
                widgetList: ["user_affinity"],
                postBody: `api_key=${API_KEY}&client_id="2253"&client_name="yoox-poc"&mad_uuid=${mad_UUID}&user_id=${user_id}`,
                recommendationUrl:
                  "https://eu-robin-poc.madstreetden.com/v2/user_affinity",
              });
            } else if (
              context.client_name == "14_namshi" ||
              context.client_name == "2251_flava-topfloor"
            ) {
              widget_arr.push({
                widgetList: ["9"],
                postBody: `api_key=${API_KEY}&cp_ensemble="false"&details=true&fields=["id","product_id","category","ontology","link","image_link","internal_image_url","small_image_link","msd_ontology"]&mad_uuid=${mad_UUID}&user_id=${user_id}&num_results=5&products_per_outfit=3&group_by_occasion=true&occasions=["Casual", "Brunch" , "Work Out", "Office", "Party", "Holiday"]&widget_list=[9]&product_id=${productId.current}&manually_curated="true"&out_of_stock_fallback="fill"&machine_generated_fallback="true"`,
                recommendationUrl:
                  "https://ap-southeast-1-vuestyle-staging.madstreetden.com" +
                  "/vuestyle/widgets",
              });
            } else {
              widget_arr.push({
                widgetList: ["8"],
                postBody: `api_key=${API_KEY}&cp_ensemble=true&details=true&duplicates=true&fields=["id","product_id","category","ontology","link","image_link","internal_image_url","small_image_link","msd_ontology"]&mad_uuid=${mad_UUID}&user_id=${user_id}&num_results=[15]&require_source=true&widget_list=[8]&product_id=${productId.current}&brand_filter=false&cp_random=true&user_boost_params=&is_from_demo_site=true&force_debug=true`,
                recommendationUrl: baseEndPoint + productRecommendation,
              });
            }

            widget_arr.push({
              widgetList: ["0_disableBundit"],
              postBody: `api_key=${API_KEY}&cp_ensemble=true&details=true&duplicates=true&fields=["id","product_id","category","ontology","link","image_link","internal_image_url","small_image_link", "msd_ontology"]&mad_uuid=${mad_UUID}&user_id=${user_id}&num_results=[15]&require_source=true&widget_list=[0]&use_bandits=false&product_id=${productId.current}&brand_filter=false&is_from_demo_site=true&force_debug=true`,
              recommendationUrl: baseEndPoint + similarRecommendation,
            });
            widget_arr.push({
              widgetList: ["0_enableBundit"],
              postBody: `api_key=${API_KEY}&cp_ensemble=true&details=true&duplicates=true&fields=["id","product_id","category","ontology","link","image_link","internal_image_url","small_image_link", "msd_ontology"]&mad_uuid=${mad_UUID}&user_id=${user_id}&num_results=[15]&require_source=true&widget_list=[0]&use_bandits=true&product_id=${productId.current}&brand_filter=false&is_from_demo_site=true&force_debug=true`,
              recommendationUrl: baseEndPoint + personalizedRecommendation,
            });
            updateWidgetList(widget_arr);
            updateCopyWidgetArr(widget_arr);
          }

          return (
            <RecommendedPdtsContainer recommendedWidgetList={currWidget}>
              <ProductDetailsWrapper>
                <RenderTopcontainer
                  updateimageSearchData={updateimageSearchData}
                  imageSearchData={imageSearchData}
                  productId={productId.current}
                  debug={debug}
                  ontology={ontology}
                  updateOntology={updateOntology}
                  hideTagResults={hideTagResults}
                />
                <RenderStyleItContainer
                  displayOccasion={displayOccasion}
                  updateDisplayOccasion={updateDisplayOccasion}
                  OccasionStyleData={OccasionStyleData}
                  updateOccasionStyleData={updateOccasionStyleData}
                  AllOccasionDetails={AllOccasionDetails}
                  updateAllOccasionDetails={updateAllOccasionDetails}
                  hasOnlyOccasionName={hasOnlyOccasionName}
                  updateHasOnlyOccasionName={updateHasOnlyOccasionName}
                  isModalOpen={isModalOpen}
                  updateModal={updateModal}
                ></RenderStyleItContainer>
                <RenderRecommendationContainer
                  isChecked={isChecked}
                  updateCheckbox={updateCheckbox}
                  is_affinity_checked={is_affinity_checked}
                  update_affinity_checkbox={update_affinity_checkbox}
                  force_debug={debug}
                  filterValue={filterValue}
                  updateFilterValue={updateFilterValue}
                  updateWidgetList={updateWidgetList}
                  copyWidgetArr={copyWidgetArr}
                />

                {is_affinity_checked ? (
                  <AffinityContainer>
                    <UserAffinityTitle>User Affinity</UserAffinityTitle>
                    <RenderSpiderContainer
                      is_affinity_checked={is_affinity_checked}
                    />
                    <RenderSparkLine
                      is_affinity_checked={is_affinity_checked}
                      sparkLineArr={sparkLineArr}
                      updateSparkLineArr={updateSparkLineArr}
                      ontology={ontology}
                    />
                    <RenderWordCloud
                      is_affinity_checked={is_affinity_checked}
                      ontology={ontology}
                    ></RenderWordCloud>
                    <AffinityCloseButtonContainer>
                      <Button
                        onClick={() =>
                          update_affinity_checkbox(!is_affinity_checked)
                        }
                      >
                        Close
                      </Button>
                    </AffinityCloseButtonContainer>
                  </AffinityContainer>
                ) : null}
              </ProductDetailsWrapper>
            </RecommendedPdtsContainer>
          );
        }}
      </ConfigConsumer>
    </>
  );
};

export { ProductDetails };
