const getTags = (file, configProps) => {
  let tag = DefaultValues["vue_tag_url"];
  let apiKey = DefaultValues["vue_tag_api_key"];
  let client_id = null;
  let force_facet = "";

  if (tag && file) {
    let formData;
    console.log(file);
    if (
      (typeof file === "string" && file.length) ||
      (!Array.isArray(file) && file[0])
    ) {
      if (typeof file === "string") {
        let formObj = {};
        if (file.startsWith("[")) {
          file = JSON.parse(file);
          formObj["cache_clear"] = "true";
        }

        formObj["api_key"] = apiKey;
        formObj["correlation_key"] = "";
        formObj["image_url"] = file;
        formObj["image_type"] = 1;
        formObj["image"] = file;
        formObj["client_id"] = client_id;

        if (force_facet) {
          formObj["facet"] = force_facet;
        }

        formData = JSON.stringify(formObj);
      }
    }
    return fetch(tag, {
      method: "POST",
      body: formData,
    })
      .then((data) => data.json())
      .then((resp) => resp)
      .catch((err) => console.log(err));
  }
};

const makeAPIRequests = (file, updateimageSearchData, configProps) => {
  Promise.all([getTags(file, configProps)])
    .then((response) => {
      if (response && response.length) {
        updateimageSearchData((currResp) => {
          let UploadedResp = { ...currResp };
          if (
            response[0] &&
            response[0].status &&
            response[0].status.toLowerCase() === "success"
          ) {
            UploadedResp.tagsResp = { ...response[0] };
          }
          UploadedResp.tagsResp.loading = false;
          UploadedResp.imgSrc = file;
          return UploadedResp;
        });
      }
    })
    .catch((err) => {
      console.log(err);
      updateimageSearchData((currResp) => {
        let UploadedResp = { ...currResp };
        UploadedResp.tagsResp.loading = false;
        UploadedResp.imgSrc = file;
        return UploadedResp;
      });
    });
  updateimageSearchData((currResp) => {
    console.log(currResp);
    let UploadedResp = { ...currResp };
    UploadedResp.tagsResp.loading = true;
    UploadedResp.imgSrc = file;
    return UploadedResp;
  });
};

